@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
.logo {
  display: block;
  max-width: 90px;
  height: auto;
  margin: 20px auto 0;
}

.logo:hover {
  cursor: pointer;
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 10px;
  margin-bottom: 10px;
}

.loginBtn {
  display: block;
  margin: 50px auto 0;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
  cursor: pointer;
}

.loginBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

/* upload form styles */
form {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background: white;
  justify-items: center;
  align-items: center;
  min-height: 100vh;
  padding: 40px 0;
}

input,
button,
label,
textarea {
  width: 300px;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

input[type="text"],
textarea {
  border: 1px solid var(--secondary);
  border-radius: 4px;
  display: inline-block;
  padding: 12px 14px;
  color: var(--secondary);
  font-size: 14px;
}

textarea {
  height: 150px;
  margin-top: 30px;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  border: 1px solid var(--primary);
  border-radius: 4px;
  display: inline-block;
  padding: 10px 14px;
  cursor: pointer;
  color: var(--primary);
  text-align: center;
  margin-top: 10px;
}

.custom-file-upload:hover {
  background-color: var(--primary);
  color: var(--secondary);
}

.output {
  height: 60px;
  font-size: 0.8rem;
}
.error {
  color: var(--error);
}

.addNewBtn {
  display: block;
  margin: 0 auto 0;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
}

.addNewBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

.closeForm {
  display: block;
  margin: 50px auto 50px auto;
  padding: 10px 15px;
  background-color: var(--secondary);
  border: 1px solid var(--secondary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: white;
  cursor: pointer;
}

.closeForm:hover {
  border: 1px solid var(--secondary);
  background: none;
  color: var(--secondary);
  cursor: pointer;
}

.submitForm {
  display: none;
  margin-top: 20px;
}

.thumbnail img {
  max-height: 100px;
  width: auto;
  border: none;
  margin: 20px auto;
  border: none;
  box-shadow: none;
}

@media only screen and (min-width: 800px) {
  .form {
    padding: 40px;
  }

  .thumbnail img {
    max-width: 400px;
    max-height: 100%;
  }

  input,
  label,
  textarea {
    width: 500px;
  }
}

/* image grid styles */
.img-grid {
  margin: 40px auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

.image {
  position: relative;
}

.image::after {
  content: "";
  display: block;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media only screen and (min-width: 800px) {
  .img-grid {
    grid-template-columns: 1fr 1fr;
  }

  .image:hover {
    opacity: 0.9;
  }
}

@media only screen and (min-width: 1400px) {
  .img-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

/* modal styles */
.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.backdrop img {
  display: block;
  max-width: 95%;
  max-height: auto;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

@media only screen and (min-width: 800px) {
  .backdrop img {
    display: block;
    max-width: 60%;
    max-height: 80%;
  }
}

/* STAR RATING STYLES */
.stars {
  border: 0;
  margin: 20px 0 0;
  padding: 0;
  font-size: 100%;
}

.stars li {
  float: left;
  list-style: none;
  width: 20%;
  max-width: 35px;
  line-height: 0;
  margin-bottom: 10px;
  text-align: center;
  padding: 0 5px;
  box-sizing: border-box;
}

.stars li:first-child {
  margin-left: -2px;
}

.stars li:hover {
  cursor: pointer;
}

.stars .star {
  width: auto;
  height: 100%;
  fill: #3658a25f;
}

.stars .selected .star {
  fill: #2d4f9a;
}

.rating {
  display: block;
  width: 100%;
  height: 50px;
}

.starRating {
  display: block;
  margin: 10px auto 20px auto;
  height: 50px;
  width: 355px;
}

@media only screen and (min-width: 800px) {
  .stars li {
    max-width: 40px;
  }

  .starRating {
    width: 440px;
  }
}

/***** User *****/
.userIsLoggedIn {
  position: absolute;
  top: 15px;
  right: 15px;
  overflow: hidden;
}

.userIsLoggedIn p {
  display: none;
}

.userContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  margin-top: 50px;
}

.userContent p {
  display: block;
  font-size: 1.2rem;
  padding: 0 15px;
}

@media only screen and (min-width: 800px) {
  .userContent {
    margin-top: 0;
  }
}

.userContent img {
  border-radius: 50%;
  max-width: 70px;
  width: 100%;
  height: auto;
  display: block;
}

.userPage {
  position: fixed;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  height: 100%;
  width: 290px;
  padding: 15px;
  margin-right: -350px;
  background: white;
  top: 0;
  right: 0;
  z-index: 999;
  transition-duration: 1s;
  overflow: hidden;
}

.show {
  margin-right: 0 !important;
  transition-duration: 1s;
}

/**** HAMBURGER ***/
nav {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 999999;
  width: 80px;
}

.active {
  position: fixed;
}

.logOutBtn {
  display: block;
  margin: 25px auto;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
}

.logOutBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

@media only screen and (min-width: 800px) {
  nav {
    top: 20px;
    right: 20px;
    width: 200px;
  }

  .active {
    right: 230px;
    transition-duration: 1s;
  }

  .logOutBtn {
    margin: 25px 0;
  }
}

/*!
 * Hamburgers
 * @description Tasty CSS-animated hamburgers
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/hamburgers
 * @link https://github.com/jonsuh/hamburgers
 */
.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  outline: none;
  width: 50px;
}
.hamburger:hover {
  opacity: 0.7;
}
.hamburger.is-active:hover {
  opacity: 0.7;
}
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #000;
}

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}
.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 40px;
  height: 4px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}
.hamburger-inner::before {
  top: -10px;
}
.hamburger-inner::after {
  bottom: -10px;
}

.hamburger--slider .hamburger-inner {
  top: 2px;
}
.hamburger--slider .hamburger-inner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.hamburger--slider .hamburger-inner::after {
  top: 20px;
}

.hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}
.hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
}

.whiskyDetailsContainer {
  display: flex;
  flex-direction: column;
}

.whiskyDetailsContainer li {
  list-style-type: none;
  margin-left: 0;
}

.whiskyDetailsContainer ul {
  padding-left: 0;
}

.whiskyDetailsContainer h3 {
  margin-top: 0;
}

@media only screen and (min-width: 800px) {
  .whiskyDetailsContainer {
    display: grid;
    width: 100%;
    grid-template-rows: auto;
    grid-template-columns: 300px 1fr;
    grid-gap: 20px;
  }
}

.whiskyDetailsContainer .whiskyItem {
  display: flex;
}

.whiskyContainer .whiskyItem h2 {
  text-align: center;
}

.whiskyContainer .whiskyItem .notes textarea {
  margin: 0;
  width: 90%;
  height: 90px;
}

.whiskyContainer .getWhiskyInfo {
  text-align: center;
  margin-bottom: 20px;
}

@media only screen and (min-width: 800px) {
  .whiskyContainer .whiskyItem {
    display: grid;
    width: 100%;
    grid-template-areas:
      "image header"
      "image notes"
      "image rating"
      "details details";
    grid-template-rows: 40px auto 60px 1fr;
    grid-template-columns: 300px 1fr;
    grid-gap: 20px;
  }

  .whiskyContainer .whiskyItem h2 {
    grid-area: header;
    margin: 0;
    text-align: left;
  }

  .whiskyContainer .whiskyItem .image {
    grid-area: image;
  }

  .whiskyContainer .whiskyItem .image img {
    width: 100%;
    height: auto;
  }

  .whiskyContainer .whiskyItem .rating {
    grid-area: rating;
    margin-top: -15px;
  }

  .whiskyContainer .whiskyItem .notes {
    grid-area: notes;
  }

  .whiskyContainer .whiskyItem .notes textarea {
    width: 400px;
    height: 150px;
  }

  .whiskyContainer .whiskyItem .whiskyDetailsContainer {
    grid-area: details;
  }

  .whiskyContainer .getWhiskyInfo {
    text-align: left;
    margin-bottom: 0;
  }

  .whiskyContainer .whiskyItem .noWhiskyData,
  .whiskyContainer .whiskyItem .loadingData {
    grid-area: details;
    margin-top: 60px;
    font-weight: 100;
    text-align: center;
  }
}

:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
  --medium: 800px;
}

/* base styles & title */
body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
}
.App {
  max-width: 960px;
  margin: 0 auto;
}

@media only screen and (min-width: 1400px) {
  .App {
    max-width: 1400px;
    margin: 0 auto;
  }
}

.title h1 {
  margin-top: 20px;
  font-size: 2.6rem;
}

.title h1,
.title p {
  text-align: center;
}

